import { createModel } from '@rematch/core';
import { Modal } from 'antd';
import API from '@/api';

const compare = {
  lowestprice: (a, b) => {
    if (a.price < b.price) return -1;
    if (a.price > b.price) return 1;
    return 0;
  },
  highestprice: (a, b) => {
    if (a.price > b.price) return -1;
    if (a.price < b.price) return 1;
    return 0;
  },
};

export const scaffold = createModel()({
  state: {
    // 
    sort: null,
    products: [],
    // ()
    filters: [],
    isLoading: false,
  },
  reducers: {
    updateProducts: (state, payload) => {
      return {
        ...state,
        products: payload,
      };
    },
    // 
    updateSort: (state, payload) => {
      return {
        ...state,
        sort: payload,
      };
    },
    // 
    updateFilters: (state, payload) => {
      return {
        ...state,
        filters: payload,
      };
    },
    updateLoadingState: (state, payload) => {
      return {
        ...state,
        isLoading: payload,
      };
    },
  },
  effects: (dispatch) => {
    return {
      fetchProducts: async function (_, state) {
        dispatch({ type: 'scaffold/updateLoadingState', payload: true });
        try {
          const { data } = await API.getProducts();
          let products = data.products;
          // 
          const { filters, sort } = state.scaffold;
          if (!!filters && filters.length > 0) {
            products = products.filter((p) =>
              filters.find((f) => p.availableSizes.find((size) => size === f))
            );
          }
          if (!!sort) {
            products = products.sort(compare[sort]);
          }

          dispatch({ type: 'scaffold/updateProducts', payload: products });
          dispatch({ type: 'scaffold/updateLoadingState', payload: false });
        } catch (e) {
          dispatch({ type: 'scaffold/updateLoadingState', payload: false });
          console.error(e);
          //  
          Modal.confirm({
            title: '',
            content: '',
            okText: '',
            cancelText: '',
            onOk: () => {
              this.fetchProducts();
            },
          });
          // if (window.confirm('')) {
          //   this.fetchProducts();
          // }
        }
      },
    };
  },
});
